.toggle-all {
  width: 1px;
  height: 1px;
  border: none; /* Mobile Safari */
  opacity: 0;
  position: absolute;
  right: 100%;
  bottom: 100%;
}

.toggle-all + label {
  width: 60px;
  height: 34px;
  font-size: 0;
  position: absolute;
  top: -52px;
  left: -13px;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.toggle-all + label:before {
  content: '❯';
  font-size: 22px;
  color: #e6e6e6;
  padding: 10px 27px 10px 27px;
}

.toggle-all:checked + label:before {
  color: #737373;
}

.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    position: relative;
    font-size: 24px;
    border-bottom: 1px solid #ededed;

    .icon {
      position: absolute;
      top: 0;
      bottom: -3px;
      width: 30px;
      height: 40px;
      font-size: 34px;
      color: #cc9a9a;
      margin: auto 0 11px;
      transition: color 0.2s ease-out;
      cursor: pointer;

      &:hover {
        color: #af5b5e;
      }
    }

    .icon-destroy {
      right: 10px;

      &:after {
        content: '×';
      }
    }

    .icon-edit {
      font-size: 19px;
      right: 45px;

      &:after {
        content: '✎';
      }
    }

    .edit {
      display: none;
    }

  }
}
